import React from 'react';
import { Field, Form, Formik } from 'formik';
import * as yup from 'yup';
import { getAuth, createUserWithEmailAndPassword } from 'firebase/auth';
import axios from 'axios';

import { BigButton } from '../../componenst';
import { Link, useNavigate } from 'react-router-dom';

import styles from './RegisterForm.module.scss';

type CreateAccountForm = {
  gender: string;
  name: string;
  lastName: string;
  phoneNumber: string;
  day: string;
  month: string;
  year: string;
  email: string;
  pass: string;
  confirmPass: string;
  terms: boolean;
  loyalty: boolean;
  news: boolean;
};

const phoneRegExp =
  /^(\+7|7|8)?[\s\\-]?\(?[489][0-9]{2}\)?[\s\\-]?[0-9]{3}[\s\\-]?[0-9]{2}[\s\\-]?[0-9]{2}$/;

const RegisterForm: React.FC = () => {
  const navigate = useNavigate();
  const RegisterFormValidation = yup.object().shape({
    gender: yup.string().required(' '),
    name: yup
      .string()
      .matches(/\D/i, '     ')
      .required(' '),
    lastName: yup
      .string()
      .matches(/\D/i, '     ')
      .required(' '),
    phoneNumber: yup
      .string()
      .matches(phoneRegExp, '  ')
      .required(' '),
    day: yup.string().required(' '),
    month: yup.string().required(' '),
    year: yup.string().required(' '),
    email: yup.string().email(' email').required(' '),
    pass: yup
      .string()
      .min(8, ` 8 `)
      .matches(/[A-Z-]+/u, '   ')
      .matches(/[a-z-]+/u, '   ')
      .matches(/\W+/, '   "!";%:?*()_+{}[]<>?/.,"')
      .required(' '),
    confirmPass: yup.string().oneOf([yup.ref('pass')], '  '),
    terms: yup
      .bool()
      .oneOf([true], '     .')
      .required(''),
  });

  const fullYear = React.useRef(new Date().getFullYear());

  const days: string[] = [];
  while (days.length < 31) days.push(`${days.length + 1}`);

  const months: string[] = [];
  while (months.length < 12) months.push(`${months.length + 1}`);

  let years: string[] = [];
  while (years.length < 100) years.push(`${fullYear.current - 18 - years.length}`);

  return (
    <div className={styles.create_account__form}>
      <h2> </h2>
      <p>        </p>
      <Formik
        initialValues={{
          gender: '',
          name: '',
          lastName: '',
          phoneNumber: '',
          day: '',
          month: '',
          year: '',
          email: '',
          pass: '',
          confirmPass: '',
          terms: false,
          loyalty: false,
          news: false,
        }}
        onSubmit={(values: CreateAccountForm) => {
          const auth = getAuth();
          createUserWithEmailAndPassword(auth, values.email, values.pass)
            .then((userCredential) => {
              axios.post(`https://62e206223891dd9ba8def88d.mockapi.io/user`, {
                ...values,
                uid: userCredential.user.uid,
                birthDay: values.day + '.' + values.month + '.' + values.year,
              });
              navigate('../auth');
            })
            .catch((error) => {
              console.error(error);
            });
        }}
        validationSchema={RegisterFormValidation}>
        {({ values, handleBlur, handleChange, handleSubmit, errors, touched, isValid, dirty }) => (
          <Form>
            <div className={styles.gender_block}>
              <label htmlFor="male">
                <Field type="radio" name="gender" id="male" value="male" />
                
              </label>
              <label htmlFor="female">
                <Field type="radio" name="gender" id="female" value="female" />
                
              </label>
              {touched.gender && errors.gender && <p className={styles.error}>{errors.gender}</p>}
            </div>
            <label htmlFor="name">
              
              <Field
                type="text"
                name="name"
                placeholder=""
                onChange={handleChange}
                onBlur={handleBlur}
                value={values.name}
                required
              />
              {touched.name && errors.name && <p className={styles.error}>{errors.name}</p>}
            </label>
            <label htmlFor="lastName">
              
              <Field
                type="text"
                name="lastName"
                placeholder=""
                onChange={handleChange}
                onBlur={handleBlur}
                value={values.lastName}
                required
              />
              {touched.lastName && errors.lastName && (
                <p className={styles.error}>{errors.lastName}</p>
              )}
            </label>
            <label htmlFor="phoneNumber">
              
              <Field
                type="text"
                name="phoneNumber"
                placeholder="+79553654955"
                onChange={handleChange}
                onBlur={handleBlur}
                value={values.phoneNumber}
                required
              />
              {touched.phoneNumber && errors.phoneNumber && (
                <p className={styles.error}>{errors.phoneNumber}</p>
              )}
            </label>
            <label className={styles.date_block}>
              <span> </span>
              <select
                name="day"
                title=" "
                id="day"
                onChange={handleChange}
                onBlur={handleBlur}
                value={values.day}
                required>
                <option value=""></option>
                {days.map((day) => (
                  <option key={day} value={day}>
                    {day}
                  </option>
                ))}
              </select>
              <select
                name="month"
                title=" "
                id="month"
                onChange={handleChange}
                onBlur={handleBlur}
                value={values.month}
                required>
                <option value=""></option>
                {months.map((month) => (
                  <option key={month} value={month}>
                    {month}
                  </option>
                ))}
              </select>
              <select
                name="year"
                title=" "
                id="year"
                onChange={handleChange}
                onBlur={handleBlur}
                value={values.year}
                required>
                <option value=""></option>
                {years.map((year) => (
                  <option key={year} value={year}>
                    {year}
                  </option>
                ))}
              </select>
            </label>
            {(touched.day || touched.month || touched.year) &&
              (errors.day || errors.month || errors.year) && (
                <p className={styles.error}>{errors.day || errors.month || errors.year}</p>
              )}
            <label htmlFor="email">
              Email
              <Field
                type="text"
                name="email"
                onChange={handleChange}
                onBlur={handleBlur}
                value={values.email}
                placeholder="myawesomeemail@email.com"
              />
              {touched.email && errors.email && <p className={styles.error}>{errors.email}</p>}
            </label>
            <label htmlFor="pass">
              
              <Field
                type="password"
                name="pass"
                onChange={handleChange}
                onBlur={handleBlur}
                value={values.pass}
                placeholder="********"
                required
              />
              {touched.pass && errors.pass && <p className={styles.error}>{errors.pass}</p>}
            </label>
            <label htmlFor="confirmPass">
               
              <Field
                type="password"
                name="confirmPass"
                onChange={handleChange}
                onBlur={handleBlur}
                value={values.confirmPass}
                placeholder="********"
                required
              />
              {touched.confirmPass && errors.confirmPass && (
                <p className={styles.error}>{errors.confirmPass}</p>
              )}
            </label>
            <div className={styles.divider} />
            <label htmlFor="terms">
              <Field type="checkbox" name="terms" id="terms" required />
              <div>
                <p>       </p>
              </div>
            </label>
            <label htmlFor="news">
              <Field type="checkbox" name="news" id="news" />
              <div>
                <p>      </p>
              </div>
            </label>
            <label htmlFor="loyalty">
              <Field type="checkbox" name="loyalty" id="loyalty" />
              <div>
                <p>       Sushidream</p>
              </div>
            </label>
            {touched.terms && errors.terms && <p className={styles.error}>{errors.terms}</p>}

            <BigButton text={' '} anyFunc={handleSubmit} isFormValid={isValid} />
          </Form>
        )}
      </Formik>
      <h3>
         <Link to="../auth"></Link>
      </h3>
    </div>
  );
};

export default RegisterForm;
